<template>
    <div class='home' v-if="bannerImg.length>0">
        <div class="banner" :style="{'background':'url('+ imgUrl+bannerImg[0].image +')'}">
            <div class="logo">
                <!-- <img src="../assets/img/shangcheng/logo1.png" alt=""> -->
            </div>
            <div>
                <div class="sousuo1">
                    <span class="hr"></span>
                    <el-input :placeholder="$t('shouye.banner[0]')" v-model="input">
                        <el-button slot="append" icon="el-icon-search" @click="sousuo()" style="color: #fff;" size="medium"></el-button>    
                    </el-input>
                </div>
                <div class="cen">
                    <div class="list">
                        <div>{{$t('shouye.banner[1]')}}</div>
                        <div class="item" v-for="(item,i) in hot_searches" :key="i" @click="sousuo(item)">{{item}}</div>
                    </div>
                </div>
            </div>
        </div>
        <div class="yewu">
            <div class="typeTitle">
                {{$t('shouye.title[0]')}}
                <span></span>
            </div>
            <div class="typeText" v-if="yewu">
                {{yewu.content}}
            </div>
            <div class="list">
                <div class="item" v-for="(item,i) in yewu.list" :key="i">
                    <div class="img">
                        <img :src="imgUrl + item.icon" alt="" loading='../assets/img/shangcheng/blue.png'>
                    </div>
                    <h1>{{item.title}}</h1>
                    <div class="itemText">{{item.intro}}</div>
                </div>
            </div>
        </div>
        <div class="center" :style="{'background':'url('+ imgUrl+bannerImg[1].image +')'}">
            <h1>{{$t('shouye.title[1]')}}</h1>
            <div v-if="Info">
                {{Info.main.content}}
            </div>
        </div>
        <div class="chanpin">
            <div class="typeTitle">
                {{$t('shouye.title[2]')}}
                <span></span>
            </div>
            <div class="list">
                <div class="listType">
                    <div v-for="(item,i) in get_first_category" :key="i" @click="getGoods_list(item.id)">{{item.category_name}}</div>
                </div>
                <div class="listPro">
                    <div class="listItem" @click="xianging(item.id)" v-for="(item,i) in goods_list" :key="i">
                        <transition name="el-zoom-in-center">
                            <div v-show="list" class="transition-box pro">
                                <div><img v-lazy="imgUrl + item.thumb" alt=""></div>
                                <h1>{{item.name}}</h1>
                                <p>{{item.sku}}</p>
                            </div>
                        </transition>
                    </div>
                </div>
            </div>
        </div>
        <div class="moshi" :style="{'background':'url('+ imgUrl+bannerImg[2].image +')'}">
            <h1>{{$t('shouye.title[3]')}}</h1>
            <div class="moshiList">
                <div class="item" v-for="(item,i) in mode.list" :key="i">
                    <div class="img">
                        <img v-lazy="imgUrl + item.icon" alt="">
                        <span class="next"></span>
                    </div>
                    <p>{{i+1}}.{{item.title}}</p>
                    <div class="text">
                        {{item.intro}}
                    </div>
                </div>
            </div>
        </div>
        <div class="qiye">
            <div class="typeTitle">
                {{$t('shouye.title[4]')}}
                <span></span>
            </div>
            <!-- <div class="typeText">
                河南志尔贝进出口贸易有限公司成立于2017年03月06日，公司成立之初，背靠国家一带一路政策立足于巴基斯坦，基于中巴形式的和谐政策，在董事长马志方
                的带领下，公司凭借过硬的品质和完善的管理，市场销量节节攀升。公司总部设于河南省郑州市郑东新区建正东方中心C座1305.1306室，开设了义乌办事处，
                深圳办事处外加驻巴基斯坦首都伊斯兰堡分公司及仓库，卡拉奇仓库。
            </div> -->
            <div class="gengduo">
                <span @click="$router.push({path:'xinwenList'})">更多></span>
            </div>
            <div class="qiyeList">
                <div class="item" @click="$router.push({path:'xinwen',query:{id:item.id}})" v-for="(item,i) in news" :key="i">
                    <div class="img">
                        <img :src="imgUrl + item.thumb" alt="">
                    </div>
                    <!-- <h1>{{item.title}}</h1> -->
                    <div class="itemText" style="margin-top: 15px;">{{item.title}}</div>
                </div>
            </div>
        </div>
        <div class="toptop">
            <img src="../assets/img/shangcheng/huidingbu.png" alt="">
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            imgUrl:'',//图片ip
            input:'',//搜索关键词
            list:false,
            Info:'',//全部
            yewu:'',//主营业务
            mode:'',//赚钱模式
            news:'',//新闻动态
            hot_searches:'',//搜索关键词
            get_first_category:'',//一级分类
            goods_list:'',//商品列表
            bannerImg:'',
        };
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        _this.imgUrl = _this.apis.second;
        //全局信息
        console.log(_this.apis.lang)
        _this.apis.get(_this.apis.message,{params:{lang:_this.apis.lang}}).then(function(res) {
            if(res.data.code == 0){
                _this.Info = res.data.data
                _this.yewu = res.data.data.scope
                _this.mode = res.data.data.mode
            }
        })
        //背景图
        _this.apis.get(_this.apis.banner,{params:{lang:_this.apis.lang}}).then(function(res) {
            if(res.data.code == 0){
                console.log(res.data)
                _this.bannerImg = res.data.data
            }
        })
        //新闻动态
        _this.apis.get(_this.apis.news,{params:{lang:_this.apis.lang}}).then(function(res) {
            if(res.data.code == 0){
                _this.news = res.data.data
            }
        })
        //搜索关键词
        _this.apis.get(_this.apis.hot_searches,{params:{lang:_this.apis.lang}}).then(function(res) {
            if(res.data.code == 0){
                _this.hot_searches = res.data.data
            }
        })
        //一级分类
        _this.apis.get(_this.apis.get_first_category,{params:{lang:_this.apis.lang}}).then(function(res) {
            if(res.data.code == 0){
                _this.get_first_category = res.data.data
                _this.getGoods_list(_this.get_first_category[0].id)
            }
        })
    },
    watch:{
    },
    methods:{
        xianging(id){
            var _this = this;
            _this.$router.push({
                path:'xiangqing',
                query:{id:id,title:'商品详情'}
            })
        },
        sousuo(key){
            var _this = this;
            if(key){
                _this.$router.push({
                    path:'list',
                    query:{title:'官方商城',key:key}
                })
            }else{
                _this.$router.push({
                    path:'list',
                    query:{title:'官方商城',key:_this.input}
                })
            }
        },  
        //商品列表
        getGoods_list(id){
            var _this = this;
            _this.list = true;
            _this.apis.get(_this.apis.goods_list,{params:{lang:_this.apis.lang,id:id}}).then(function(res) {
                if(res.data.code == 0){
                    _this.goods_list = res.data.data
                    _this.list = true;
                }
            })
        },
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
@keyframes border{
    from {border:1px solid red;}
    to {border:1px solid yellow;}
}
.toptop{
    position: fixed;
    bottom: 50px;
    right: 50px;
}
.home{
    .banner{
        height: 564px;
        width: 100%;
        margin-bottom: 88px;
        padding-top: 113px;
        text-align: center;
        background-image: url(../assets/img/banner.png);
        background-size: 100% 100%;
        .logo{
            width: 444px;
            height: 87px;
            margin: 0 auto;
            margin-bottom: 30px;
        }
        .sousuo1{
            width: 670px;
            height: 55px;
            margin: 0 auto;
            margin-bottom: 20px;
            position: relative;
            .hr{
                display: inline-block;
                width: 2px;
                height: 20px;
                background: rgba(255, 255, 255, 0.35);
                position: absolute;
                top: 50%;
                right: 50px;
                transform: translateY(-50%);
            }
            button{
                transform: scale(1.3);
            }
        }
        .cen{
            color: #cccccc;
            .list{
                width: 670px;
                margin: 0 auto;
                display: flex;
                justify-content: left;
                padding-left: 94px;
                div{
                    margin-right: 32px;
                }
                .item{
                    margin-right: 32px;
                    cursor:pointer;
                }
            }
        }
    }
    .typeTitle{
            font-size: 30px;
            width: 1200px;
            height: 80px;
            line-height: 80px;
            margin: 0 auto;
            margin-bottom: 50px;
            text-align: center;
            position: relative;
            span{
                width: 90px;
                height: 4px;
                position: absolute;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                background: #FFAD00; 
            }
        }
    .typeText{
        margin-bottom: 30px;
        font-size: 14px;
        line-height: 30px;
        letter-spacing: 1px;
        padding: 0 20px;
        text-align: center;
    }
    .yewu{
        width: 1200px;
        margin: 0 auto;
        .list{
            text-align: center;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .item{
                padding: 0 25px;
                .img{
                    width: 112px;
                    height: 112px;
                    margin: 0 auto;
                }
                h1{
                    margin: 30px 0;
                    font-size: 20px;
                }
            }
        }
    }
    .center{
        width: 100%;
        height: 320px;
        text-align: center;
        margin: 98px 0 88px;
        padding: 45px 0;
        background-image: url(../assets/img/chanpinBanner.png);
        background-size: 100% 100%;
        h1{
            color: #FFAD23;
            font-size: 28px;
            line-height: 100px;
            margin-bottom: 25px;
        }
        div{
            width: 800px;
            margin: 0 auto;
            color: #f1f1f1; 
            font-size: 14px;
            line-height: 20px;
        }
    }
    .chanpin{
        .list{
            width: 1200px;
            margin: 0 auto;
            .listType{
                display: flex;
                justify-content: center;
                align-items: center;
                div{
                    padding: 5px 10px;
                    margin: 0 10px;
                    border: 1px solid #EBEBEB;
                    cursor:pointer;
                }
                div:hover{
                    transition:all 0.5s border;
                }
            }
            .listPro{
                margin: 80px 0;
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                align-items: center;
                .listItem{
                    width: 300px;
                    height: 294px;
                    cursor:pointer;
                }
                .listItem:hover h1{
                    color: #FFAD00;
                    text-decoration:underline;
                }
                .listItem:hover p{
                    color: #FFAD00;
                    text-decoration:underline;
                }
                .pro{
                    padding: 0 50px;
                    div{
                        width: 200px;
                        height: 200px;
                    }
                    h1{
                        font-size: 14px;
                        color: #000;
                        text-align: center;
                        margin-top: 15px;
                    }
                    p{
                        font-size: 12px;
                        color: #959595;
                        text-align: center;
                    }
                }
            }
        }
    }
    .moshi{
        width: 100%;
        height: 465px;
        text-align: center;
        margin: 90px 0;
        padding-top: 30px;
        background-image: url(../assets/img/chanpinBanner.png);
        background-size: 100% 100%;
        h1{
            margin: 20px 0;
            color: #FFAD23;
            font-size: 36px;
            line-height: 100px;
            margin-bottom: 45px;
        }
        .moshiList{
            color: #f1f1f1;
            width: 1303px;
            margin: 0 auto;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        .moshiList>div:last-child{
            .next{
                display: none !important;
            }
        }
        .item{
            width: 300px;
            padding: 0 20px;
            .img{
                width: 102px;
                height: 102px;
                margin: 0 auto;
                position: relative;
                .next{
                    display: inline-block;
                    width: 13px;
                    height: 21px;
                    position: absolute;
                    top: 50%;
                    right: -125px;
                    // transform: translateY(-50%);
                    // border: 15px solid #FFAD23;
                    // border-color: transparent transparent transparent #FFAD23;
                    background-image: url(../assets/img/shangcheng/sanjiao.png);
                    background-size: 100% 100%;
                }
                img{
                    border-radius: 50%;
                }
            }
            p{
                color: #f1f1f1;
                font-size: 20px;
                margin: 20px 0;
                margin-bottom: 4px;
            }
            .text{
                font-size: 14px;
                line-height: 30px;
                padding: 0 10px;
            }
        }
    }
    .qiye{
        width: 1200px;
        margin: 0 auto;
        .gengduo{
            text-align: right;
            color: #FF8C53;
            font-size: 18px;
            line-height: 20px;
            span{
                cursor:pointer
            }
        }
        .qiyeList{
            padding-top: 40px;
            margin-bottom: 80px;
            text-align: center;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: center;
            .item{
                width: 380px;
                margin-bottom: 30px;
                cursor:pointer;
                .img{
                    width: 100%;
                    height: 264px;
                    margin: 0 auto;
                }
                h1{
                    margin: 20px 0;
                    font-size: 18px;
                }
            }
        }
    }
}
</style>
<style>
    .el-input__inner{
        background: none;
    }
</style>


